<template>
  <div>
    <Button block dangerText @click="isShowReAuthModal = true">
      
    </Button>
    <p class="small mt-4"></p>
    <p class="small mt-4" style="color: #eb486b" v-if="message">
      
    </p>
    <Modal :show="isShowReAuthModal" @close="isShowReAuthModal = false">
      <template slot="header">
        </template
      >
      <template slot="body">
        <Button block danger @click="reauth"></Button>
      </template>
    </Modal>
    <Modal :show="isShowDeleteModal" @close="isShowDeleteModal = false">
      <template slot="header">
        </template
      >
      <template slot="body">
        <p style="font-weight: 700">{{ userName }}</p>
        <p class="small">
          Twitter  Google
          
        </p>
        <ul class="small">
          <li>
            <a
              target="_blank"
              rel="noopener"
              href="https://help.twitter.com/ja/managing-your-account/connect-or-revoke-access-to-third-party-apps"
              >Twitter </a
            >
          </li>
          <li>
            <a
              target="_blank"
              rel="noopener"
              href="https://support.google.com/accounts/answer/3466521?hl=ja"
              >Google </a
            >
          </li>
        </ul>
        <Button block danger @click="deleteAccount"
          ></Button
        >
      </template>
    </Modal>
  </div>
</template>

<script>
import Button from "../components/Button";
import Modal from "../components/Modal";

import firebase from "../plugins/firebase";
import Auth from "../utils/auth";
const db = firebase.firestore();

export default {
  components: {
    Button,
    Modal,
  },
  props: {
    userName: String,
    user: Object,
    logout: Function,
  },
  data() {
    return {
      isShowReAuthModal: false,
      isShowDeleteModal: false,
      message: "",
    };
  },
  methods: {
    reauth() {
      const user = firebase.auth().currentUser;
      const providerId = user.providerData[0].providerId;

      const provider =
        providerId === "google.com"
          ? new firebase.auth.GoogleAuthProvider()
          : new firebase.auth.TwitterAuthProvider();

      user
        .reauthenticateWithPopup(provider)
        .then(() => {
          // User re-authenticated.
          this.isShowReAuthModal = false;
          this.isShowDeleteModal = true;
        })
        .catch(() => {
          // An error ocurred
          // ...
        });
    },
    deleteAccount() {
      this.massage = "";

      db.collection("users")
        .doc(this.user.uid)
        .delete()
        .then(() => {
          Auth.delete();
          this.logout();
        })
        .catch((error) => {
          this.massage = `: , ${error}`;
        });
    },
  },
};
</script>
